.authorization {
  padding-bottom: 25px;
  width: 390px;
  min-height: 450px;
  border-radius: 5px;
  background: #fff;
  position: fixed;
  top: 45%;
  left: 50%;
  margin-top: -130px;
  margin-left: -195px;
  display: none;
  opacity: 0;
  z-index: 20;
}
.authorization .typeOfAuthor {
  margin-bottom: 30px;
}
.authorization .typeOfAuthor ul {
  display: flex;
  margin-bottom: 40px;
}
.authorization .typeOfAuthor ul li {
  width: 50%;
}
.authorization .typeOfAuthor ul li a {
  display: inline-block;
  width: 100%;
  height: 65px;
  font: 1.125em/65px "PTSans-Bold";
  color: #98a2a4;
  text-align: center;
  text-decoration: none;
  background-color: #ebebeb;
  border-radius: 5px;
}
.authorization .typeOfAuthor ul li a:hover {
  outline: none;
}
.authorization .author,
.authorization .regist {
  text-align: center;
}
.authorization .author p,
.authorization .regist p {
  position: relative;
  margin: 10px 0;
}
.authorization .author p .user,
.authorization .regist p .user,
.authorization .author p .shield,
.authorization .regist p .shield,
.authorization .author p .phone,
.authorization .regist p .phone,
.authorization .author p .envelope,
.authorization .regist p .envelope {
  position: absolute;
  top: 15px;
  left: 85px;
  color: #00929e;
}
.authorization .author p input,
.authorization .regist p input {
  width: 200px;
  height: 40px;
  padding-left: 50px;
  font: 1em/40px "PTSans-Regular";
  color: #828282;
  border: 1px solid #e6e6e6;
  border-radius: 5px;
  outline: none;
}
.authorization .author p select,
.authorization .regist p select {
  width: 250px;
  height: 40px;
  padding-left: 25px;
  font: 1em/40px "PTSans-Regular";
  color: #828282;
  border: 1px solid #e6e6e6;
  border-radius: 5px;
  outline: none;
}
.authorization .author p .asterisk,
.authorization .regist p .asterisk {
  position: absolute;
  top: 20px;
  right: 85px;
  font-size: .35em;
  color: #ff0000;
}
.authorization .author p label .checkbox,
.authorization .regist p label .checkbox {
  display: none;
}
.authorization .author p label .checkbox-custom,
.authorization .regist p label .checkbox-custom {
  position: relative;
  width: 20px;
  height: 20px;
  border: 1px solid #e6e6e6;
  border-radius: 5px;
}
.authorization .author p label .checkbox-custom,
.authorization .regist p label .checkbox-custom,
.authorization .author p label .label,
.authorization .regist p label .label {
  display: inline-block;
  vertical-align: middle;
}
.authorization .author p label .checkbox:checked + .checkbox-custom:before,
.authorization .regist p label .checkbox:checked + .checkbox-custom:before {
  content: "";
  display: block;
  position: absolute;
  top: 2px;
  right: 2px;
  bottom: 2px;
  left: 2px;
  background-color: #00929e;
  border-radius: 5px;
}
.authorization .author button,
.authorization .regist button {
  margin: 30px 0;
  width: 250px;
  height: 40px;
  font: 1em/1.125em "PTSans-Regular";
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background-color: #00929e;
  border: 1px solid #00929e;
  border-radius: 5px;
  outline: none;
}
.authorization .author button:hover,
.authorization .regist button:hover {
  cursor: pointer;
}
.authorization .author a,
.authorization .regist a {
  display: block;
  font: 0.875em/1em "PTSans-Regular";
  color: #828282;
}
.authorization .ui-tabs-active .ui-tabs-anchor {
  color: #00929e !important;
  background-color: #fff !important;
  outline: none;
}
.authorization .socialAuthor {
  text-align: center;
}
.authorization .socialAuthor p {
  margin: 25px 0;
  font: 1em/1.125em "PTSans-Bold";
  color: #00929e;
}
.authorization .socialAuthor ul {
  display: flex;
  justify-content: center;
}
.authorization .socialAuthor ul li a {
  margin: 0 10px;
  display: inline-block;
  width: 140px;
  height: 40px;
  font: 1em/40px "PTSans-Regular";
  color: #fff;
  text-align: center;
  text-decoration: none;
  border-radius: 5px;
}
.authorization .socialAuthor ul li .facebook {
  background-color: #3b5998;
  border-bottom: 1px solid #293e6a;
}
.authorization .socialAuthor ul li .gPlus {
  background-color: #db4437;
  border-bottom: 1px solid #8a251d;
}
